<template>
  <div class="edit container">
  <div class="col-md-8 m-auto">
      <router-link to="/components" class="btn btn-primary btn-sm">Вернуться</router-link>
  <h1 class="page-header mb-4">Редактирование комплектующего</h1>
  <form v-on:submit="updateComponent">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4 class="ml-3">Информация о комплектующем</h4>
            <div class="form-group mb-3 col-md-auto">
                <label>Наименование комплектующего</label>
                <input type="text" class="form-control" placeholder="наименование комплектующего" v-model="component.name_component">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Страна-производитель</label>
                <input type="text" class="form-control" placeholder="страна-производитель" v-model="component.producing_country">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Стоимость продажи</label>
                <input type="text" class="form-control" placeholder="стоимость продажи" v-model="component.selling_price">
            </div>
        </div>
        <button type="применить" class="btn btn-primary col-md-12">Применить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        component: {},
        alert:''
        }
    },
    methods: {
        fetchComponent(id){
            this.$http.get('http://slimapp/api/component/'+id)
            .then(function(response){
                this.component = response.body;
            });
        },
      updateComponent(e){
          if(!this.component.name_component || !this.component.producing_country || !this.component.selling_price){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let updComponent = {
                    name_component: this.component.name_component,
                    producing_country: this.component.producing_country,
                    selling_price: this.component.selling_price
                }
                this.$http.put('http://slimapp/api/component/update/'+this.$route.params.id, updComponent)
                    .then(function(response){
                        this.$router.push({path: '/components', query: {alert: 'Комплектующее обновлено'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchComponent(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

