<template>
  <div class="details container">
    
    <router-link to="/customers" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{customer.last_name}} {{customer.first_name}} {{customer.second_name}}
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/'+customer.id_customer">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteCustomer(customer.id_customer)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
          <div class="form-group">
                <label>Номер телефона:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-phone" aria-hidden="true"></span> {{customer.phone}}</li>
          </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'customerdetails',
  data () {
    return {
      customer: ''
    }
  },
  methods: {
      fetchCustomer(id){
          this.$http.get('http://slimapp/api/customer/'+id)
          .then(function(response){
            this.customer = response.body;
          });
      },
      deleteCustomer(id){
          this.$http.delete('http://slimapp/api/customer/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/customers', query: {alert: 'Клиент удален'}});
          });
      }
  },
  created: function(){
      this.fetchCustomer(this.$route.params.id);
  }
}
</script>

