<template>
  <div class="add container">
      <div class="col-md-8 m-auto">
  <router-link to="/repair log" class="btn btn-primary btn-sm">Вернуться</router-link>
  <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Добавить лог ремонта</h1>
  <form v-on:submit="addReplog">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о логе ремонта</h4>
            <div class="form-group">
                <label>Номер регистрации</label>
                <input type="text" class="form-control" placeholder="номер регистрации" v-model="replog.id_registry">
            </div>
            <div class="form-group">
                <label>Номер сотрудника</label>
                <input type="text" class="form-control" placeholder="номер сотрудника" v-model="replog.id_employee">
            </div>
            <div class="form-group">
                <label>Дата</label>
                <input type="text" class="form-control" placeholder="дата" v-model="replog.Date">
            </div>
             <div class="form-group">
                <label>Время</label>
                <input type="text" class="form-control" placeholder="время" v-model="replog.Time">
            </div>
            <div class="form-group">
                <label>Статус</label>
                <input type="text" class="form-control" placeholder="статус" v-model="replog.status">
            </div>
        </div>
        <button type="добавить" class="btn btn-primary col-md-12">Добавить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        replog: {},
        alert:''
        }
    },
    methods: {
      addReplog(e){
          if(!this.replog.id_registry || !this.replog.id_employee || !this.replog.Date || !this.replog.Time || !this.replog.status){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let newReplog = {
                    id_registry: this.replog.id_registry,
                    id_employee: this.replog.id_employee,
                    Date: this.replog.Date,
                    Time: this.replog.Time,
                    status: this.replog.status
                }
                this.$http.post('http://slimapp/api/repair_log/add', newReplog)
                    .then(function(response){
                        this.$router.push({path: '/repair log', query: {alert: 'Лог ремонта добавлен'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
        }
    },
    components:{
        Alert
    }
}
</script>

