// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import vueResource from 'vue-resource'
import Customers from './components/Customers'
import About from './components/About'
import Add from './components/Add'
import Edit from './components/Edit'
import CustomerDetails from './components/CustomerDetails'
import Components from './components/Components'
import Request from './components/Request'
import RequestDetails from './components/RequestDetails'
import ComponentDetails from './components/ComponentDetails'
import EditComponent from './components/EditComponent'
import AddComp from './components/AddComp'
import Registry_for_repair from './components/Registry_for_repair'
import Registry_for_repairDetails from './components/Registry_for_repairDetails'
import EditRegistry_for_repair from './components/EditRegistry_for_repair'
import AddRegistry_for_repair from './components/AddRegistry_for_repair'
import Repair_log from './components/Repair_log'
import Repair_logDetails from './components/Repair_logDetails'
import EditRepair_log from './components/EditRepair_log'
import AddRepair_log from './components/AddRepair_log'
import Repair_place from './components/Repair_place'
import Repair_placeDetails from './components/Repair_placeDetails'
import EditRepair_place from './components/EditRepair_place'
import AddRepair_place from './components/AddRepair_place'
import Staff from './components/Staff'
import StaffDetails from './components/StaffDetails'
import EditStaff from './components/EditStaff'
import AddStaff from './components/AddStaff'
import Technique from './components/Technique'
import TechniqueDetails from './components/TechniqueDetails'
import EditTechnique from './components/EditTechnique'
import AddTechnique from './components/AddTechnique'
import Login from './components/Login'
import Home from './components/Home'
import VueSession from 'vue-session'
import RepairLogUSER from './components/RepairLogUSER'
 
Vue.use(VueSession)
Vue.use(vueResource)
Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    {path:'/Customers', component: Customers},
    {path:'/about', component: About},
    {path:'/add/customer', component: Add},
    {path: '/customer/:id', component: CustomerDetails},
    {path: '/edit/:id', component: Edit},
    { path: "*", component: Home },
    { path: "/", component: Home },

    { path: "/zayavka", component: Request },
    { path: "/zayavka/:id", component: RequestDetails },
    { path: "/repuser", component: RepairLogUSER },
    

    {path: '/components', component: Components},
    {path: '/component/:id', component: ComponentDetails},
    {path: '/edit/component/:id', component: EditComponent},
    {path: '/add/component', component: AddComp},

    {path: '/registry for repair', component: Registry_for_repair},
    {path: '/registry/:id', component: Registry_for_repairDetails},
    {path: '/edit/registry for repair/:id', component: EditRegistry_for_repair},
    {path: '/add/registry', component: AddRegistry_for_repair},

    {path: '/repair log', component: Repair_log},
    {path: '/replog/:id', component: Repair_logDetails},
    {path: '/edit/repair log/:id', component: EditRepair_log},
    {path: '/add/repair log', component: AddRepair_log},

    {path: '/repair place', component: Repair_place},
    {path: '/repplace/:id', component: Repair_placeDetails},
    {path: '/edit/repair place/:id', component: EditRepair_place},
    {path: '/add/repair place', component: AddRepair_place},

    {path: '/staff', component: Staff},
    {path: '/staf/:id', component: StaffDetails},
    {path: '/edit/staff/:id', component: EditStaff},
    {path: '/add/staff', component: AddStaff},

    {path: '/technique', component: Technique},
    {path: '/technique/:id', component: TechniqueDetails},
    {path: '/edit/technique/:id', component: EditTechnique},
    {path: '/add/technique', component: AddTechnique},

    {path: '/login', component: Login},
    {path: '/home', component: Home},
  ]
})

/* eslint-disable no-new */
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')