<template>
  <div class="details container">
    
    <router-link to="/zayavka" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{req.last_name}} {{req.first_name}} {{req.second_name}}
        <span class="pull-right">
            <button class="btn btn-danger" v-on:click="deleteReq(req.id_request)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
          <div class="form-group">
                <label>Номер телефона:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-phone" aria-hidden="true"></span> {{req.phone}}</li>
                <label>Текст проблемы:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-text" aria-hidden="true"></span> {{req.text_problem}}</li>
          
          </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'requestdetails',
  data () {
    return {
      req: ''
    }
  },
  methods: {
      fetchReq(id){
          this.$http.get('http://slimapp/api/zayavki/'+id)
          .then(function(response){
            this.req = response.body;
          });
      },
      deleteReq(id){
          this.$http.delete('http://slimapp/api/zayavki/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/zayavka', query: {alert: 'Заявка удалена'}});
          });
      }
  },
  created: function(){
      this.fetchReq(this.$route.params.id);
  }
}
</script>

