<template>
  <div class="edit container">
  <div class="col-md-8 m-auto">
      <router-link to="/registry for repair" class="btn btn-primary btn-sm">Вернуться</router-link>
  <h1 class="page-header">Редактирование регистрации</h1>
  <form v-on:submit="updateRegistry">
    <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о регистрации</h4>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер клиента</label>
                <input type="text" class="form-control" placeholder="номер клиента" v-model="registry.id_customer">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер сотрудника</label>
                <input type="text" class="form-control" placeholder="номер сотрудника" v-model="registry.id_employee">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер техники</label>
                <input type="text" class="form-control" placeholder="номер техники" v-model="registry.id_technology">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер места ремонта</label>
                <input type="text" class="form-control" placeholder="номер места ремонта" v-model="registry.id_repair_place">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер запчасти</label>
                <input type="text" class="form-control" placeholder="номер запчасти" v-model="registry.id_spares">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Дата и время</label>
                <input type="text" class="form-control" placeholder="дата и время" v-model="registry.created">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Стоимость</label>
                <input type="text" class="form-control" placeholder="стоимость" v-model="registry.value">
            </div>
        </div>
        <button type="применить" class="btn btn-primary col-md-12">Применить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        registry: {},
        alert:''
        }
    },
    methods: {
        fetchRegistry(id){
            this.$http.get('http://slimapp/api/registry_for_repair/'+id)
            .then(function(response){
                this.registry = response.body;
            });
        },
      updateRegistry(e){
          if(!this.registry.id_customer || !this.registry.id_employee || !this.registry.id_technology || !this.registry.id_repair_place || !this.registry.id_spares || !this.registry.created || !this.registry.value){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let updRegistry = {
                    id_customer: this.registry.id_customer,
                    id_employee: this.registry.id_employee,
                    id_technology: this.registry.id_technology,
                    id_repair_place: this.registry.id_repair_place,
                    id_spares: this.registry.id_spares,
                    created: this.registry.created,
                    value: this.registry.value
                }
                this.$http.put('http://slimapp/api/registry_for_repair/update/'+this.$route.params.id, updRegistry)
                     .then(function(response){
                        this.$router.push({path: '/registry for repair', query: {alert: 'Регистрация обновлена'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchRegistry(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

