<template>
  <div class="add container">
       <div class="col-md-8 m-auto">
  <router-link to="/repair place" class="btn btn-primary btn-sm">Вернуться</router-link>
  <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Добавить место ремонта</h1>
  <form v-on:submit="addRepplace">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о месте ремонта</h4>
            <div class="form-group">
                <label>Номер сотрудника</label>
                <input type="text" class="form-control" placeholder="номер сотрудника" v-model="repplace.id_employee">
            </div>
            <div class="form-group">
                <label>Адрес</label>
                <input type="text" class="form-control" placeholder="адрес" v-model="repplace.address">
            </div>
            <div class="form-group">
                <label>Наименование места</label>
                <input type="text" class="form-control" placeholder="нименование места" v-model="repplace.name_place">
            </div>
        </div>
        <button type="добавить" class="btn btn-primary col-md-12">Добавить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        repplace: {},
        alert:''
        }
    },
    methods: {
      addRepplace(e){
          if(!this.repplace.id_employee || !this.repplace.address || !this.repplace.name_place){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let newRepplace = {
                    id_employee: this.repplace.id_employee,
                    address: this.repplace.address,
                    name_place: this.repplace.name_place
                }
                this.$http.post('http://slimapp/api/repair_place/add', newRepplace)
                    .then(function(response){
                        this.$router.push({path: '/repair place', query: {alert: 'Место добавлено'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
        }
    },
    components:{
        Alert
    }
}
</script>

