<template>
  <div class="details container">
    
    <router-link to="/staff" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{staf.last_name}} {{staf.first_name}} {{staf.second_name}}
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/staff/'+staf.id_employee">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteStaf(staf.id_employee)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
            <div class="form-group">
                <label>Опыт работы:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-exp" aria-hidden="true"></span> {{staf.exp}}</li>
            </div>
            <div class="form-group">
                <label>Номер телефона:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-phone" aria-hidden="true"></span> {{staf.phone}}</li>
            </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'staffdetails',
  data () {
    return {
      staf: ''
    }
  },
  methods: {
      fetchStaf(id){
          this.$http.get('http://slimapp/api/staff/'+id)
          .then(function(response){
            this.staf = response.body;
          });
      },
      deleteStaf(id){
          this.$http.delete('http://slimapp/api/staff/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/staff', query: {alert: 'Сотрудник удален'}});
          });
      }
  },
  created: function(){
      this.fetchStaf(this.$route.params.id);
  }
}
</script>

