<template>
  <div class="staff container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление сотрудниками</h1>
  <input class="form-control" placeholder="Введите фамилию" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Фамилия</th>
        <th>Имя</th>
        <th>Отчество</th>
        <th>Опыт работы</th>
        <th>Номер телефона</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="staf in filterBy(staff, filterInput)">
        <td>{{staf.last_name}}</td>
        <td>{{staf.first_name}}</td>
        <td>{{staf.second_name}}</td>
        <td>{{staf.exp}}</td>
        <td>{{staf.phone}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/staf/'+staf.id_employee">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'staff',
    data () {
      return {
        staff: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchStaff(){
      this.$http.get('http://slimapp/api/staff')
      .then(function(response){
        if(this.$session.get("user_id")){
    this.staff = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
      });
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(staf){
        return staf.last_name.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchStaff();
  },
  updated: function(){
    this.fetchStaff();
  },
components: {
  Alert
}
}
</script>

