<template>
  <div class="details container">
    
    <router-link to="/registry for repair" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{registry.customer_name}} 
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/registry for repair/'+registry.id_registry">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteRegistry(registry.id_registry)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
          <div class="form-group">
                <label>Фамилия сотрудника:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-id employee" aria-hidden="true"></span> {{registry.employee_name}}</li>
          </div>
           <div class="form-group">
                <label>Наименование техники:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-id technology" aria-hidden="true"></span> {{registry.technique_name}}</li>
            </div>
            <div class="form-group">
                <label>Адрес места ремонта:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-id repair place" aria-hidden="true"></span> {{registry.address_name}}</li>
            </div>
            <div class="form-group">
                <label>Наименование запчасти:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-id spares" aria-hidden="true"></span> {{registry.component_name}}</li>
            </div>
             <div class="form-group">
                <label>Дата и время:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-created" aria-hidden="true"></span> {{registry.created}}</li>
            </div>
            <div class="form-group">
                <label>Стоимость:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-value" aria-hidden="true"></span> {{registry.value}}</li>
            </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'registry_for_repairdetails',
  data () {
    return {
      registry: ''
    }
  },
  methods: {
      fetchRegistry(id){
          this.$http.get('http://slimapp/api/registry_for_repair/'+id)
          .then(function(response){
            this.registry = response.body;
          });
      },
      deleteRegistry(id){
          this.$http.delete('http://slimapp/api/registry_for_repair/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/registry for repair', query: {alert: 'Регистрация удалена'}});
          });
      }
  },
  created: function(){
      this.fetchRegistry(this.$route.params.id);
  }
}
</script>

