<template>
  <div class="components container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление комплектующими</h1>
  <input class="form-control" placeholder="Введите наименование комплектующего" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Наимнование комплектующего</th>
        <th>Страна-производитель</th>
        <th>Стоимость продажи</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="component in filterBy(components, filterInput)">
        <td>{{component.name_component}}</td>
        <td>{{component.producing_country}}</td>
        <td>{{component.selling_price}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/component/'+component.id_component">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'components',
    data () {
      return {
        components: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchComponents(){
      this.$http.get('http://slimapp/api/components')
      .then(function(response){
       if(this.$session.get("user_id")){
      this.components = response.body;
      }else{
      this.$router.push({path: '/login'});
    }
  });
},
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(component){
        return component.name_component.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchComponents();
  },
  updated: function(){
    this.fetchComponents();
  },
components: {
  Alert
}
}
</script>

