<template>
  <div class="edit container">
      <div class="col-md-8 m-auto">
      <router-link to="/repair place" class="btn btn-primary btn-sm">Вернуться</router-link>
  <h1 class="page-header">Редактирование места ремонта</h1>
  <form v-on:submit="updateRepplace">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о месте ремонта</h4>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер сотрудника</label>
                <input type="text" class="form-control" placeholder="номер сотрудника" v-model="repplace.id_employee">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Адрес</label>
                <input type="text" class="form-control" placeholder="адрес" v-model="repplace.address">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Наименование места</label>
                <input type="text" class="form-control" placeholder="наименование места" v-model="repplace.name_place">
            </div>
        </div>
        <button type="применить" class="btn btn-primary col-md-12">Применить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        repplace: {},
        alert:''
        }
    },
    methods: {
        fetchRepplace(id){
            this.$http.get('http://slimapp/api/repair_place/'+id)
            .then(function(response){
                this.repplace = response.body;
            });
        },
      updateRepplace(e){
          if(!this.repplace.id_employee || !this.repplace.address || !this.repplace.name_place){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let updRepplace = {
                    id_employee: this.repplace.id_employee,
                    address: this.repplace.address,
                    name_place: this.repplace.name_place
                }
                this.$http.put('http://slimapp/api/repair_place/update/'+this.$route.params.id, updRepplace)
                    .then(function(response){
                        this.$router.push({path: '/repair place', query: {alert: 'Место ремонта обновлено'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchRepplace(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

