<template>
  <div class="details container">
    
    <router-link to="/repair log" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{replog.id_registry}} 
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/repair log/'+replog.id_log">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteReplog(replog.id_log)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
            <div class="form-group">
                <label>Фамилия сотрудника:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-id employee" aria-hidden="true"></span> {{replog.employee_name}}</li>
            </div>
            <div class="form-group">
                <label>Дата:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-Date" aria-hidden="true"></span> {{replog.Date}}</li>
            </div>
            <div class="form-group">
                <label>Время:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-Time" aria-hidden="true"></span> {{replog.Time}}</li>
            </div>
            <div class="form-group">
                <label>Статус выполннения:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-status" aria-hidden="true"></span> {{replog.status}}</li>
            </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'repair_logdetails',
  data () {
    return {
      replog: ''
    }
  },
  methods: {
      fetchReplog(id){
          this.$http.get('http://slimapp/api/repair_log/'+id)
          .then(function(response){
            this.replog = response.body;
          });
      },
      deleteReplog(id){
          this.$http.delete('http://slimapp/api/repair_log/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/repair log', query: {alert: 'Лог ремонта удален'}});
          });
      }
  },
  created: function(){
      this.fetchReplog(this.$route.params.id);
  }
}
</script>

