<template>
  <div class="zav container">
      <div class="col-md-8 m-auto">
  <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header mb-4">Оставить заявку</h1>
  <form v-on:submit="addHom">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация для заявки</h4>
            <div class="form-group mb-3 col-md-auto">
                <label>Фамилия</label>
                <input type="text" class="form-control" placeholder="Фамилия" v-model="hom.last_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Имя</label>
                <input type="text" class="form-control" placeholder="Имя" v-model="hom.first_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Отчество</label>
                <input type="text" class="form-control" placeholder="Отчество" v-model="hom.second_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер телефона</label>
                <input type="text" class="form-control" placeholder="номер телефона" v-model="hom.phone">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Текст проблемы</label>
                <input type="text" class="form-control" placeholder="Текст проблемы" v-model="hom.text_problem">
            </div>
        </div>
        <button type="добавить" class="btn btn-primary col-md-12">Отправить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        hom: {},
        alert:''
        }
    },
    methods: {
      addHom(e){
          if(!this.hom.last_name || !this.hom.first_name || !this.hom.second_name || !this.hom.phone || !this.hom.text_problem){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let newHom = {
                    last_name: this.hom.last_name,
                    first_name: this.hom.first_name,
                    second_name: this.hom.second_name,
                    phone: this.hom.phone,
                    text_problem: this.hom.text_problem
                }
                this.$http.post('http://slimapp/api/home', newHom)
                    .then(function(response){
                        this.alert = 'Заявка отправлена';
                        this.hom.last_name = '';
                        this.hom.first_name = '';
                        this.hom.second_name = '';
                        this.hom.phone = '';
                        this.hom.text_problem = '';
                        this.$router.push({path: '/home'});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
    components:{
        Alert
    }
    }
</script>

