<template>
  <div class="technique container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление техникой</h1>
  <input class="form-control" placeholder="Введите наименование техники" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Наименование техники</th>
        <th>Производитель</th>
        <th>Серийный номер</th>
        <th>Год изготовления</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="tech in filterBy(technique, filterInput)">
        <td>{{tech.name}}</td>
        <td>{{tech.producer}}</td>
        <td>{{tech.serial_number}}</td>
        <td>{{tech.Year_of_manufacture}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/technique/'+tech.id_technique">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'technique',
    data () {
      return {
        technique: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchTechnique(){
      this.$http.get('http://slimapp/api/technique')
      .then(function(response){
        if(this.$session.get("user_id")){
    this.technique = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
      });
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(tech){
        return tech.name.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchTechnique();
  },
  updated: function(){
    this.fetchTechnique();
  },
components: {
  Alert
}
}
</script>

