<template>
  <div class="add container">
       <div class="col-md-8 m-auto">
  <router-link to="/technique" class="btn btn-primary btn-sm">Вернуться</router-link>
  <h1 class="page-header">Добавить технику</h1>
  <form v-on:submit="addTech">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о технике</h4>
            <div class="form-group">
                <label>Нименование техники</label>
                <input type="text" class="form-control" placeholder="наименование техники" v-model="tech.name">
            </div>
            <div class="form-group">
                <label>Производитель</label>
                <input type="text" class="form-control" placeholder="производитель" v-model="tech.producer">
            </div>
            <div class="form-group">
                <label>Серийный номер</label>
                <input type="text" class="form-control" placeholder="серийный номер" v-model="tech.serial_number">
            </div>
            <div class="form-group">
                <label>Год изготовления</label>
                <input type="text" class="form-control" placeholder="год изготовления" v-model="tech.Year_of_manufacture">
            </div>
        </div>
        <button type="добавить" class="btn btn-primary col-md-12">Добавить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        tech: {},
        alert:''
        }
    },
    methods: {
      addTech(e){
          if(!this.tech.name || !this.tech.producer || !this.tech.serial_number || !this.tech.Year_of_manufacture){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let newTech = {
                    name: this.tech.name,
                    producer: this.tech.producer,
                    serial_number: this.tech.serial_number,
                    Year_of_manufacture: this.tech.Year_of_manufacture
                }
                this.$http.post('http://slimapp/api/technique/add', newTech)
                    .then(function(response){
                        this.$router.push({path: '/technique', query: {alert: 'Техника добавлена'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
    components:{
        Alert
    }
    }
</script>

