<template>
  <div class="registry for repair container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление регистрацией ремонта</h1>
  <input class="form-control" placeholder="Введите фамилию клиента" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Фамилия клиента</th>
        <th>Фамилия сотрудника</th>
        <th>Наименование техники</th>
        <th>Адрес места ремонта</th>
        <th>Наименование запчасти</th>
        <th>Дата и время</th>
        <th>Стоимость</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="registry in filterBy(registry_for_repair, filterInput)">
        <td>{{registry.customer_name}}</td>
        <td>{{registry.employee_name}}</td>
        <td>{{registry.technique_name}}</td>
        <td>{{registry.address_name}}</td>
        <td>{{registry.component_name}}</td>
        <td>{{registry.created}}</td>
        <td>{{registry.value}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/registry/'+registry.id_registry">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'registry_for_repair',
    data () {
      return {
        registry_for_repair: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchRegistry_for_repair(){
      this.$http.get('http://slimapp/api/registry_for_repair')
      .then(function(response){
       if(this.$session.get("user_id")){
    this.registry_for_repair = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
});
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(registry){
        return registry.customer_name.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchRegistry_for_repair();
  },
  updated: function(){
    this.fetchRegistry_for_repair();
  },
components: {
  Alert
}
}
</script>

