<template>
  <div class="details container">
    
    <router-link to="/components" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{component.name_component}} 
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/component/'+component.id_component">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteComponent(component.id_component)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
            <div class="form-group">
                <label>Страна-произовдитель:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-страна-производитель" aria-hidden="true"></span> {{component.producing_country}}</li>
            </div>
            <div class="form-group">
                <label>Цена продажи:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-стоимость продажи" aria-hidden="true"></span> {{component.selling_price}}</li>
          </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'componentdetails',
  data () {
    return {
      component: ''
    }
  },
  methods: {
      fetchComponent(id){
          this.$http.get('http://slimapp/api/component/'+id)
          .then(function(response){
            this.component = response.body;
          });
      },
      deleteComponent(id){
          this.$http.delete('http://slimapp/api/component/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/components', query: {alert: 'Комплектующее удалено'}});
          });
      }
  },
  created: function(){
      this.fetchComponent(this.$route.params.id);
  }
}
</script>

