<template>
  <div class="repair_log container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление логом ремонта</h1>
  <input class="form-control" placeholder="Введите номер регистрации" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Номер регистрации</th>
        <th>Фамилия сотрудника</th>
        <th>Дата</th>
        <th>Время</th>
        <th>Статус</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="replog in filterBy(repair_log, filterInput)">
        <td>{{replog.id_registry}}</td>
        <td>{{replog.employee_name}}</td>
        <td>{{replog.Date}}</td>
        <td>{{replog.Time}}</td>
        <td>{{replog.status}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/replog/'+replog.id_log">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'repair_log',
    data () {
      return {
        repair_log: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchRepair_log(){
      this.$http.get('http://slimapp/api/repair_log')
      .then(function(response){
       if(this.$session.get("user_id")){
    this.repair_log = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
});
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(replog){
        return replog.id_registry.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchRepair_log();
  },
  updated: function(){
    this.fetchRepair_log();
  },
components: {
  Alert
}
}
</script>

