<template>
  <div class="login container">
      <Alert v-if="alert" v-bind:message="alert" />
      <center> <img src="//i.imgur.com/KOigWMH.jpg"  width="180" height="180" class="d-inline-block align-top"> </center>
    <div class="row">
    <div class="col-md-6 m-auto">
        <h1 class="page-header">Авторизация</h1>
        <form v-on:submit="Login">
            <div class="well">
                <div class="form-group">
                    <label>Логин</label>
                    <input type="text" class="form-control" placeholder="логин" v-model="log.login">
                </div>
                <div class="form-group">
                    <label>Пароль</label>
                    <input type="password" class="form-control" placeholder="пароль" v-model="log.password">
                </div>
            </div>
            <button type="применить" class="btn btn-primary col-md-12">Войти</button>
        </form>
        </div>
    </div>
  </div>
</template>

<script>
import Alert from './Alert'

export default {
    name: 'login',
    data () {
        return {
        log: {},
        alert:''
        }
    },
    methods: {
        Login(e){
             if(!this.log.login || !this.log.password){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let newLog = {
                    login: this.log.login,
                    password: this.log.password
                }
                this.$http.post('http://slimapp/api/login', newLog) 
                .then(function(response){ 
                if(response.status == 200){ 
                this.$session.start() 
                this.$session.set("user_id",response.body) 
                this.$router.push({path: '/customers', query: {alert: 'Добро пожаловать'}}); 
                }else{ 
                this.$router.push({path: '/login'});
                this.alert = 'нет такого пользователя'; 
                this.log.login = ''; 
                this.log.password = '';   
            } 
        });

                e.preventDefault();
            }
            e.preventDefault();
        }
    },
    components:{
        Alert
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .m-atuo{
        margin: 0 auto;
    }
</style>
