<template>
  <div class="add container">
      <div class="col-md-8 m-auto">
  <router-link to="/components" class="btn btn-primary btn-sm">Вернуться</router-link>
  <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Добавить комплектующие</h1>
  <form v-on:submit="addComponent">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о комплектующих</h4>
            <div class="form-group">
                <label>Наименование комплектующего</label>
                <input type="text" class="form-control" placeholder="Наименование комплектующего" v-model="component.name_component">
            </div>
            <div class="form-group">
                <label>Страна-произовдитель</label>
                <input type="text" class="form-control" placeholder="Страна-произовдитель" v-model="component.producing_country">
            </div>
            <div class="form-group">
                <label>Цена продажи</label>
                <input type="text" class="form-control" placeholder="Цена продажи" v-model="component.selling_price">
            </div>
        </div>
        <button type="добавить" class="btn btn-primary col-md-12">Добавить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        component: {},
        alert:''
        }
    },
    methods: {
      addComponent(e){
          if(!this.component.name_component || !this.component.producing_country || !this.component.selling_price){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                // const fd = new FormData();
                // fd.append('image', this.selectedFile, thiis.selectedFile.name);
                let newComponent = {
                    name_component: this.component.name_component,
                    producing_country: this.component.producing_country,
                    selling_price: this.component.selling_price
                }
                this.$http.post('http://slimapp/api/component/add', newComponent)
                    .then(function(response){
                        this.$router.push({path: '/components', query: {alert: 'Комплектующие добавлены'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
        }
    },
    components: {
        Alert
    }
}
</script>

