<template>
  <div class="details container">
    
    <router-link to="/technique" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{tech.name}}
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/technique/'+tech.id_technique">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteTech(tech.id_technique)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
            <div class="form-group">
                <label>Производитель:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-producer" aria-hidden="true"></span> {{tech.producer}}</li>
            </div>
            <div class="form-group">
                <label>Серийный номер:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-serial number" aria-hidden="true"></span> {{tech.serial_number}}</li>
            </div>
             <div class="form-group">
                <label>Год изготовления:</label>
                <li class="list-group-item"><span class="glyphicon glyphicon-Year of manufacture" aria-hidden="true"></span> {{tech.Year_of_manufacture}}</li>
          </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'techniquedetails',
  data () {
    return {
      tech: ''
    }
  },
  methods: {
      fetchTech(id){
          this.$http.get('http://slimapp/api/technique/'+id)
          .then(function(response){
            this.tech = response.body;
          });
      },
      deleteTech(id){
          this.$http.delete('http://slimapp/api/technique/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/technique', query: {alert: 'Техника удалена'}});
          });
      }
  },
  created: function(){
      this.fetchTech(this.$route.params.id);
  }
}
</script>

