<template>
  <div class="details container">
    
    <router-link to="/repair place" class="btn btn-primary btn-sm">Вернуться</router-link>
    <h1 class="page-header">{{repplace.employee_name}} 
        <span class="pull-right">
            <router-link class="btn btn-primary" v-bind:to="'/edit/repair place/'+repplace.id_repair_place">Редактировать</router-link>
            <button class="btn btn-danger" v-on:click="deleteRepplace(repplace.id_repair_place)">Удалить</button>
            </span>
    </h1>
    <ul class="list-group">
      <div class="form-group">
          <label>Адрес:</label>
          <li class="list-group-item"><span class="glyphicon glyphicon-address" aria-hidden="true"></span> {{repplace.address}}</li>
      </div>
      <div class="form-group">
          <label>Наименование места:</label>
          <li class="list-group-item"><span class="glyphicon glyphicon-name_place" aria-hidden="true"></span> {{repplace.name_place}}</li>
      </div>
        </ul>
  </div>
</template>

<script>
export default {
  name: 'repair_placedetails',
  data () {
    return {
      repplace: ''
    }
  },
  methods: {
      fetchRepplace(id){
          this.$http.get('http://slimapp/api/repair_place/'+id)
          .then(function(response){
            this.repplace = response.body;
          });
      },
      deleteRepplace(id){
          this.$http.delete('http://slimapp/api/repair_place/delete/'+id)
          .then(function(response){
            this.$router.push({path: '/repair place', query: {alert: 'Место ремонта удалено'}});
          });
      }
  },
  created: function(){
      this.fetchRepplace(this.$route.params.id);
  }
}
</script>

