<template>
  <div class="edit container">
  <div class="col-md-8 m-auto">
       <router-link to="/customers" class="btn btn-primary btn-sm">Вернуться</router-link>
  <h1 class="page-header">Редактирование клиента</h1>
  <form v-on:submit="updateCustomer">
  <div class="card border-secondary mb-3 p-2" style="max-width: 45rem;">
            <h4>Информация о клиенте</h4>
            <div class="form-group mb-3 col-md-auto">
                <label>Фамилия</label>
                <input type="text" class="form-control" placeholder="фамилия" v-model="customer.last_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Имя</label>
                <input type="text" class="form-control" placeholder="имя" v-model="customer.first_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Отчество</label>
                <input type="text" class="form-control" placeholder="отчество" v-model="customer.second_name">
            </div>
            <div class="form-group mb-3 col-md-auto">
                <label>Номер телефона</label>
                <input type="text" class="form-control" placeholder="номер телефона" v-model="customer.phone">
            </div>
        </div>
        <button type="применить" class="btn btn-primary col-md-12">Применить</button>
    </form>
  </div>
  </div>
</template>

<script>
import Alert from './Alert'
    export default {
    name: 'add',
    data () {
        return {
        customer: {},
        alert:''
        }
    },
    methods: {
        fetchCustomer(id){
            this.$http.get('http://slimapp/api/customer/'+id)
            .then(function(response){
                this.customer = response.body;
            });
        },
      updateCustomer(e){
          if(!this.customer.last_name || !this.customer.first_name || !this.customer.second_name || !this.customer.phone){
                this.alert = 'Пожалуйста, заполните все обязательные поля';
            } else {
                let updCustomer = {
                    last_name: this.customer.last_name,
                    first_name: this.customer.first_name,
                    second_name: this.customer.second_name,
                    phone: this.customer.phone
                }
                this.$http.put('http://slimapp/api/customer/update/'+this.$route.params.id, updCustomer)
                    .then(function(response){
                        this.$router.push({path: '/customers', query: {alert: 'Клиент обновлен'}});
                    });

                e.preventDefault();
            }
            e.preventDefault();
    }
},
created: function(){
        this.fetchCustomer(this.$route.params.id);
    },
    components:{
        Alert
    }
    }
</script>

