<template>
  <div class="repair_place container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление местом ремонта</h1>
  <input class="form-control" placeholder="Введите фамилию сотрудника" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Фамилия сотрудника</th>
        <th>Адрес</th>
        <th>Наименование места</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="repplace in filterBy(repair_place, filterInput)">
        <td>{{repplace.employee_name}}</td>
        <td>{{repplace.address}}</td>
        <td>{{repplace.name_place}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/repplace/'+repplace.id_repair_place">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'repair_place',
    data () {
      return {
        repair_place: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchRepair_place(){
      this.$http.get('http://slimapp/api/repair_place')
      .then(function(response){
        if(this.$session.get("user_id")){
    this.repair_place = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
});
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(repplace){
        return repplace.employee_name.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchRepair_place();
  },
  updated: function(){
    this.fetchRepair_place();
  },
components: {
  Alert
}
}
</script>

