<template>
  <div class="request container">
   <Alert v-if="alert" v-bind:message="alert" />
  <h1 class="page-header">Управление заявками на звонок</h1>
  <input class="form-control" placeholder="Введите фамилию" v-model="filterInput">
  <br />
  <table class="table table-striped table-bordered">
      <thead>
       <tr>
        <th>Фамилия</th>
        <th>Имя</th>
        <th>Отчество</th>
        <th>Номер телефона</th>
        <th>Текст проблемы</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="req in filterBy(request, filterInput)">
        <td>{{req.last_name}}</td>
        <td>{{req.first_name}}</td>
        <td>{{req.second_name}}</td>
        <td>{{req.phone}}</td>
        <td>{{req.text_problem}}</td>
        <td><router-link class="btn btn-primary" v-bind:to="'/zayavka/'+req.id_request">Подробнее</router-link></td>
      </tr>
    </tbody>
  </table>
 </div>
</template>

<script>
 import Alert from './Alert';
  export default {
    name: 'request',
    data () {
      return {
        request: [],
        alert:'',
        filterInput:''
      }
    },
    methods: {
    fetchRequest(){
      this.$http.get('http://slimapp/api/zayavki')
    .then(function(response){
    if(this.$session.get("user_id")){
    this.request = response.body;
    }else{
    this.$router.push({path: '/login'});
  }
});
    },
    filterBy(list, value){
      value = value.charAt(0).toUpperCase() + value.slice(1);
      return list.filter(function(req){
        return req.last_name.indexOf(value)>-1;
      });
    }
  },
  created: function(){
    
    if(this.$route.query.alert){
      this.alert=this.$route.query.alert;
    }
    this.fetchRequest();
  },
  updated: function(){
    this.fetchRequest();
  },
components: {
  Alert
}
}
</script>

